document.addEventListener('DOMContentLoaded', () => {
  const openBtn = document.querySelector('.mobile-menu-btn');
  const closeBtn = document.querySelector('.mobile-menu-close');
  const mobileMenu = document.getElementById('mobileMenu');
  const headerBtn = document.querySelector('.header-btn'); // desktop-–∫–Ω–æ–ø–∫–∞
  const header = document.querySelector('.header'); // sticky header

  // –Ø–∫—â–æ –≤ –º–æ–±—ñ–ª—å–Ω–æ–º—É –º–µ–Ω—é —î –∫–Ω–æ–ø–∫–∞ –∞–±–æ –ª—ñ–Ω–∫ –¥–æ pets:

  const mobilePetsBtn = document.querySelector(
    '.mobile-nav-btn, .mobile-nav a[href="#pets"]'
  );

  const isSticky = el => {
    if (!el) return false;
    const pos = getComputedStyle(el).position;
    return pos === 'sticky' || pos === 'fixed';
  };

  const getHeaderOffset = () => (isSticky(header) ? header.offsetHeight : 0);
  const openMenu = () => {
    if (!mobileMenu) return;
    mobileMenu.classList.add('active');
    mobileMenu.setAttribute('aria-hidden', 'false');
    document.body.style.overflow = 'hidden';
  };

  const closeMenu = () => {
    if (!mobileMenu) return;
    mobileMenu.classList.remove('active');
    mobileMenu.setAttribute('aria-hidden', 'true');
    document.body.style.overflow = '';
  };

  const smoothScrollToId = id => {
    const target = document.getElementById(id);
    if (!target) return;
    const y =
      window.pageYOffset +
      target.getBoundingClientRect().top -
      getHeaderOffset();
    window.scrollTo({ top: y, behavior: 'smooth' });
  };

  // –í—ñ–¥–∫—Ä–∏—Ç–∏/–∑–∞–∫—Ä–∏—Ç–∏ –º–µ–Ω—é
  openBtn?.addEventListener('click', openMenu);
  closeBtn?.addEventListener('click', closeMenu);

  // –ö–ª—ñ–∫ –ø–æ –±–µ–∫–¥—Ä–æ–ø—É (–ø–æ–∑–∞ –∫–æ–Ω—Ç–µ–Ω—Ç–æ–º –º–µ–Ω—é)
  mobileMenu?.addEventListener('click', e => {
    if (e.target === mobileMenu) closeMenu();
  });

  // –ó–∞–∫—Ä–∏—Ç—Ç—è –ø–æ Esc
  document.addEventListener('keydown', e => {
    if (e.key === 'Escape' && mobileMenu?.classList.contains('active')) {
      closeMenu();
    }
  });

  // –ö–ª—ñ–∫ –ø–æ desktop-–∫–Ω–æ–ø—Ü—ñ ¬´–í–∑—è—Ç–∏ –¥—Ä—É–≥–∞¬ª
  headerBtn?.addEventListener('click', e => {
    e.preventDefault();
    smoothScrollToId('pets');
  });

  // –ö–ª—ñ–∫ –ø–æ –ø—É–Ω–∫—Ç—É/–∫–Ω–æ–ø—Ü—ñ –≤ –º–æ–±—ñ–ª—å–Ω–æ–º—É –º–µ–Ω—é ‚Üí —Å–∫—Ä–æ–ª + –∑–∞–∫—Ä–∏—Ç–∏ –º–µ–Ω—é
  mobilePetsBtn?.addEventListener('click', e => {
    e.preventDefault();
    closeMenu();
    smoothScrollToId('pets');
  });

  // –£–Ω—ñ–≤–µ—Ä—Å–∞–ª—å–Ω–æ: –≤—Å—ñ —è–∫—ñ—Ä–Ω—ñ –ª—ñ–Ω–∫–∏ –≤ –¥–æ–∫—É–º–µ–Ω—Ç—ñ
  document.querySelectorAll('a[href^="#stories"]').forEach(link => {
    link.addEventListener('click', e => {
      const hash = link.getAttribute('href');
      const id = hash?.slice(1);
      if (!id) return;
      e.preventDefault();
      closeMenu();
      smoothScrollToId(id);
    });
  });
});
