import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';
import * as basicLightbox from 'basiclightbox';
import axios from 'axios';

export function openOrderModal(animalId) {
  const modalTemplate = document.querySelector('.backdrop-order').innerHTML;

  const instance = basicLightbox.create(modalTemplate, {
    onShow: instance => {
      const modalElement = instance.element();
      const form = modalElement.querySelector('.modal-form');
      const closeBtn = modalElement.querySelector('.modal-order-close');

      // –ó–∞–∫—Ä–∏—Ç—Ç—è –ø–æ –∫–Ω–æ–ø—Ü—ñ
      closeBtn.addEventListener('click', () => instance.close());

      // –û–±—Ä–æ–±–∫–∞ ESC
      function onEscClose(e) {
        if (e.key === 'Escape') {
          instance.close();
        }
      }

      document.body.style.overflow = 'hidden';
      document.addEventListener('keydown', onEscClose);

      // –í–∏–¥–∞–ª–µ–Ω–Ω—è —Å—Ç–∏–ª—ñ–≤ –ø—Ä–∏ –∑–∞–∫—Ä–∏—Ç—Ç—ñ
      instance.element().addEventListener('basiclightbox:close', () => {
        document.body.style.overflow = '';
        document.removeEventListener('keydown', onEscClose);
      });

      // –û–±—Ä–æ–±–∫–∞ –≤—ñ–¥–ø—Ä–∞–≤–∫–∏ —Ñ–æ—Ä–º–∏
      form.addEventListener('submit', async e => {
        e.preventDefault();

        const nameInput = form.querySelector('[name="name"]');
        const phoneInput = form.querySelector('[name="phone"]');
        const commentInput = form.querySelector('[name="comment"]');

        // –û—á–∏—Å—Ç–∏—Ç–∏ –ø–æ–ø–µ—Ä–µ–¥–Ω—ñ –ø–æ–º–∏–ª–∫–∏
        nameInput.classList.remove('error');
        phoneInput.classList.remove('error');

        const nameL = nameInput.value.trim();
        const phoneL = phoneInput.value.trim();
        const commentL = commentInput.value.trim();

        // –ü–µ—Ä–µ–≤—ñ—Ä–∫–∞
        if (!nameL || !phoneL) {
          if (!nameL) nameInput.classList.add('error');
          if (!phoneL) phoneInput.classList.add('error');

          iziToast.error({
            message: "–ë—É–¥—å –ª–∞—Å–∫–∞, –∑–∞–ø–æ–≤–Ω—ñ—Ç—å —ñ–º'—è —Ç–∞ –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω—É!",
            position: 'topRight',
            timeout: 3000,
            zindex: 999999,
            appendTo: document.body,
          });
          return;
        }

        // –ù–∞–¥—Å–∏–ª–∞–Ω–Ω—è –¥–∞–Ω–∏—Ö
        const phoneClean = phoneL.replace(/\D/g, '');
        if (phoneClean.length !== 12) {
          phoneInput.classList.add('error');
          iziToast.error({
            message:
              '–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω—É –ø–æ–≤–∏–Ω–µ–Ω –º—ñ—Å—Ç–∏—Ç–∏ 12 —Ü–∏—Ñ—Ä –±–µ–∑ + —Ç–∞ –ø—Ä–æ–±—ñ–ª—ñ–≤.',
            position: 'topRight',
            timeout: 3000,
            zindex: 999999,
            appendTo: document.body,
          });
          return;
        }

        const formData = {
          name: nameL,
          phone: phoneClean,
          ...(commentL && { comment: commentL }),
          animalId,
        };

        try {
          const response = await axios.post(
            'https://paw-hut.b.goit.study/api/orders',
            formData
          );

          const orderData = response.data;

          console.log('orderData :>>', orderData);

          e.target.reset();
        } catch (error) {
          if (error.response) {
            console.error('–ü–æ–º–∏–ª–∫–∞ –≤—ñ–¥–ø–æ–≤—ñ–¥—ñ —Å–µ—Ä–≤–µ—Ä–∞:', error.response.data);
            iziToast.error({
              message: `–ü–æ–º–∏–ª–∫–∞: ${error.response.data.message}`,
              position: 'topRight',
              timeout: 3000,
              zindex: 999999,
              appendTo: document.body,
            });
          } else {
            console.error('–ü–æ–º–∏–ª–∫–∞ –∑–∞–ø–∏—Ç—É:', error.message);
            iziToast.error({
              message: `–ü–æ–º–∏–ª–∫–∞ –∑–∞–ø–∏—Ç—É: ${error.message}`,
              position: 'topRight',
              timeout: 3000,
              zindex: 999999,
              appendTo: document.body,
            });
          }
          return;
        }

        // –û—á–∏—Å—Ç–∫–∞
        form.reset();

        iziToast.success({
          title: '–£—Å–ø—ñ—Ö',
          message: '–í–∞—à–∞ –∑–∞—è–≤–∫–∞ –Ω–∞–¥—ñ—Å–ª–∞–Ω–∞!',
          position: 'topRight',
          timeout: 3000,
          zindex: 999999,
          appendTo: document.body,
        });

        instance.close();
      });

      // –í–∏–ª—É—á–µ–Ω–Ω—è –ø–æ–º–∏–ª–æ–∫ –ø—Ä–∏ –≤–≤–µ–¥–µ–Ω–Ω—ñ
      form.querySelectorAll('input, textarea').forEach(input => {
        input.addEventListener('input', () => {
          if (input.value.trim()) {
            input.classList.remove('error');
          }
        });
      });
    },
    onClose: () => {
      document.body.style.overflow = '';
    },
  });

  instance.show();
}
